<template>
  <div class="analyNav">
    <div class="container">
      <div class="title">
        <div class="title-name">
          <img
            class="logo"
            src="../assets/icon/logo-w.png"
            alt
            @click="toProject"
          />
        </div>
        <div class="user-intruduction">
          <div class="tabs">
            <!-- <router-link tag="button" :class="{ active: currentTab == 'intruduction' }" to="/intruduction">Home</router-link> -->
            <button
              :class="{ active: currentTab == 'projectSet' }"
              @click="toProject"
            >
              Project
            </button>
            <button
              :class="{ active: currentTab == 'dataPage' }"
              @click="toDataPage"
            >
              Data
            </button>
            <button
              :class="{ active: currentTab == 'library' }"
              @click="toLibrary"
            >
              Library
            </button>
            <button
              :class="{ active: currentTab == 'Analysis' }"
              @click="toAnalysis"
            >
              Analysis
            </button>
            <button
              :class="{ active: currentTab == 'myResult' }"
              @click="toResult"
            >
              Result
            </button>
            <!-- <router-link
              tag="button"
              :class="{ active: currentTab == 'setting' }"
              to="/setting"
              >Setting</router-link
            > -->
          </div>
        </div>
        <div class="user-notice">
          <div
            class="notice-img"
            v-clickoutside="isShow_1"
          >
            <img
              src="../assets/icon/notice.png"
              alt
            />
          </div>
          <div
            class="user-img"
            v-clickoutside="isShow_1"
          >
            <img
              src="../static/icon/W1.png"
              alt
              @click="handleShow_1()"
            />
            <div
              class="lists"
              v-if="isShow_1"
            >
              <router-link
                class="lists-item"
                tag="div"
                :class="{ active: currentTab == 'setting' }"
                to="/setting"
                style="border-bottom: 0.0625rem solid #e4e4e4"
              >Settings</router-link>
              <div
                class="lists-item"
                @click="dengchu"
              >Sign out</div>
            </div>
          </div>
        </div>
      </div>
      <div class="nav">
        <div class="line"></div>
        <div class="workflow">
          <slot name="navWorkflow"></slot>
        </div>
        <div
          class="project-choose"
          v-show="showId == 0"
          v-clickoutside="isShow"
        >
          <div
            class="user-name"
            @click="handleShow()"
          >
            {{ projectName }}
            <img
              style="width: 1.0625rem; height: 1.1875rem; margin-left: 0.625rem"
              src="../assets/icon/down.png"
              alt
            />
          </div>
          <!-- <div class="project-data" > -->
          <div
            class="nav-lists"
            v-if="isShow"
          >
            <vue-scroll :ops="ops">
              <div
                class="lists-name"
                v-for="item in projectData"
                :key="item.id"
                @click="handleProName(item.projectId, item.name)"
              >
                <div class="project-name">{{ item.name }}</div>
              </div>
            </vue-scroll>
          </div>
          <!-- </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import cookies from "js-cookie";
import clickoutside from "../plugins/directivesSet/clickoutside";

export default {
  name: "analyNav",
  props: {
    currentTab: String,
    showID: Number,
    mainId: Number,
  },

  data() {
    return {
      visible: false,
      placement: "left",
      // id:"null",
      projectData: [],
      showId: 1,
      isShow: false,
      isShow_1: false,
      ops: {
        vuescroll: {},
        scrollPanel: {},
        rail: {
          keepShow: true,
        },
        bar: {
          hoverStyle: true,
          onlyShowBarOnScroll: false, //是否只有滚动的时候才显示滚动条
          background: "#c9c9c9",
        },
      },
    };
  },

  computed: {
    ...mapState({
      projectId: (state) => state.projectId.proInfo.projectId,
      projectName: (state) => state.projectId.proInfo.projectName,
      navWorkflow: (state) => state.titleWorkflow.navWorkflow,
      mainID: (state) => state.mainID.mainId,
    }),
  },

  directives: {
    clickoutside,
  }, // 自定义指令

  mounted() {
    if (sessionStorage.getItem("getProjectId") != null) {
      var obj = JSON.parse(sessionStorage.getItem("getProjectId"));
      this.$store.commit("getProjectId", obj);
    }

    this.$ProjectModels
      .getProjectsModels()
      .then((res) => {
        var data = res.data.projects;
        data.forEach((ele) => {
          this.projectData.push(ele);
        });
      })
      .catch((error) => {
        this.$message.error(
          "The request list failed. Please try again or check the network." +
          "(" +
          error +
          ")",
          5
        );
      });
  },
  created() {
    this.showId = this.showID;
  },

  methods: {

    toProject() {
      this.$router.push({
        path: "/projectSet",
      });
      this.showId = 1;
      let arr = [];
      let main = 0;
      this.$emit("toProject", main);
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Project",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
    },

    toDataPage() {
      this.$router.push({
        path: "/dataPage",
      });
      this.showId = 2;
      let arr = [];
      let main = 0;
      this.$emit("toDataPage", main);
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Data",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
    },
    toLibrary() {
      this.$router.push({
        path: "/library",
      });
      this.showId = 4;
      let arr = [];
      let main = 0;
      this.$emit("toProject", main);
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Library",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);

    },
    toAnalysis() {
      this.showId = 0;
      this.$router.push({
        path: "/analysis",
      });
      let arr = [];
      let mian = 0;
      this.$emit("toAnalysis", mian);
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Analysis",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
    },

    toResult() {
      this.$router.push({
        path: "/myResult",
      });
      let arr = [];
      this.showId = 3;
      arr.push(this.navWorkflow[0]);
      let obj = {
        title: "Result",
        sign: 2,
      };
      arr.push(obj);
      this.$store.commit("postNavWorkflowData", arr);
    },

    handleShow() {
      this.isShow = !this.isShow;
    },

    handleProName(e, f) {
      //analyNav控制Analysis的filter
      let obj = {
        id: e,
        name: f,
      };
      this.$store.commit("getProjectId", obj);
      sessionStorage.setItem("getProjectId", JSON.stringify(obj));
      // sessionStorage.setItem('workflowState', JSON.stringify({workflowId:'',nodesData:{}}))
      this.isShow = false;
      window.console.log(obj.id);
      this.$ProjectModels.getAnalysesModels().then((res) => {
        let searchData = [];
        if (e == "") {
          this.searchData = res.data;
        } else {
          for (let i in res.data) {
            if (res.data[i].project.id == e) {
              searchData.push(res.data[i]);
            }
          }
        }
        this.$emit("searchDatas", searchData);
        // window.console.log(searchData);
      });
    },

    handleShow_1() {
      this.isShow_1 = !this.isShow_1;
    },

    // 退出
    dengchu() {
      cookies.remove("c_name");
      cookies.remove("iproteome_token");
      sessionStorage.removeItem("c_name");
      sessionStorage.removeItem("workflowState");
      sessionStorage.removeItem("getProjectId");
      sessionStorage.removeItem("dataProject");
      sessionStorage.removeItem("nodeId");
      let obj = {
        id: null,
        name: "Choose project",
      };
      this.$store.commit("getProjectId", obj);
      this.$router.push({ path: "/Login", query: { id: 1 } });
    },
  },
};
</script>

