/*
 * @componmentName clickoutside
 * @module "./clickoutside.js"
 * @fileDescription vue的自定义指令集，功能：点击其他的元素，当前元素隐藏。 
 * @author li
 * @date 2020年8月20日
 * @example 调用示例
 * 使用import引入之后，在vue自定义指令集注册即可
 * import clickoutside from './clickoutside';
 * directives: {
 *   clickoutside
 * }, 
 * // v-clickoutside 接受一个布尔值，通过事件控制布尔值的变化改变元素的隐藏与否。（在data函数中定义isShow）
 * <div @click="handleShow()" v-clickoutside="isShow"></div>
 * <div v-if="isShow"></div>
 */

const clickoutside = {
	// 初始化指令
    bind(el, binding, vnode) {
      el.handler = function(){
        vnode.context[binding.expression] = false;  // 找到点击的实例
      };
      el.stopProp= function(event){
        event.stopPropagation();
      };
      el.addEventListener('click', el.stopProp);
      document.body.addEventListener('click', el.handler);
    },
    update() {},
    unbind(el) {
		// 解除事件监听
      el.removeEventListener('click', el.stopProp);
      document.body.removeEventListener('click', el.handler);
    },
};

export default clickoutside; // 一个点击元素之外的元素隐藏的指令